
import AlertModule from 'vux/src/plugins/alert/module'
import Search from 'vux/src/components/search/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import axios from 'axios'
import {fileInfoTran} from '../../utils/dataTran'
export default {
components: {
Search,
Swipeout,
SwipeoutItem,
SwipeoutButton,
Tab,
TabItem
},
data () {
return {
results: [],
value: '',
list: [],
index01: 0
}
},
methods: {
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {
console.log('on-change', val)
this.results = val ? this.getResult(this.value) : []
},
dele () {
console.log(123)
},
onSubmit () {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
},
switchTabitem (index) {
console.log(index)
},
searchWorkFiles () {
axios.get(`/getFileList`, {
params: {
id: global.user.id,
type: 0
}
}).then((res) => {
if (res.data.status) {
this.list = fileInfoTran(res.data.list)
console.log(this.list)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
},
beforeCreate () {
axios.get(`${global.IP}/getFileList`, {
params: {
id: global.user.id,
type: 0
}})
.then((res) => {
if (res.data.status) {
this.list = fileInfoTran(res.data.list)
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
}



