
import 'mavon-editor/dist/css/index.css'
import {mavonEditor} from 'mavon-editor'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Radio from 'vux/src/components/radio/index.vue'

import axios from 'axios'

export default {
data () {
return {
value: '',
title: '',
fileId: '',
options: ['生活文件', '工作文件'],
show: false
}
},
components: {
mavonEditor,
XInput,
XHeader,
PopupHeader,
Popup,
Group,
Radio
},
methods: {
saveFile () {
const body = {
id: global.user.id,
title: this.title === '' ? '无标题' : this.title,
content: this.value,
type: 0,
fileType: 'md',
fileId: this.fileId
}
axios
.post(`${global.IP}/saveFile`, body)
.then((res) => {
res.data && this.$router.push({path: '/note/list'})
}).catch((error) => {
console.log(error)
})
}
}
}



