
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'

import 'font-awesome/css/font-awesome.min.css'
import Icon from 'vue-awesome/components/Icon'
import axios from 'axios'
import {userInfoTran} from '../utils/dataTran'
export default {
data () {
return {
userName: '',
userPwd: ''
}
},
methods: {
fakeLogin () {
this.$router.replace('/note/add')
},
sign () {
const body = {
name: this.userName,
pwd: this.userPwd

}
this.userName === '' || this.userPwd === ''
? AlertModule.show({
content: '请填写用户名和密码！'
})
: axios
.post(`${global.IP}/adduser`, body)
.then((res) => {
if (res.data.status) {
global.user = userInfoTran(res.data.list, true)
this.$router.push({path: '/app'})
} else {
AlertModule.show({
content: res.data.msg
})
}
}).catch((error) => {
console.log(error)
})
}
},
components: {
XInput,
Flexbox,
FlexboxItem,
Icon,
Group
}
}



